import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import Hls from 'hls.js';
import './VideoPlayer.css';
import VideoController from './VideoController';
import {
    isPlayingState,
    objectfitState,
    isVideoMenuState,
    selectedLevelState,
} from '../../providers/videoState';
import VideoMenu from './VideoMenu';


const VideoPlayer = forwardRef(({ videoUrl }, ref) => {
    const hlsRef = useRef(null);
    const videoRef = useRef(null);
    const videoPlayerRef = useRef(null);
    // useState
    const [maxWidth, setMaxWidth] = useState(960);
    const [maxHeight, setMaxHeight] = useState(540);
    const [isPlayerHovered, setIsPlayerHovered] = useState(false);
    const [resolutions, setResolutions] = useState([]);
    const [duration, setDuration] = useState(0);

    // useRecoilState
    const [selectedLevel, setSelectedLevel] = useRecoilState(selectedLevelState);
    const [objectfit, setObjectfit] = useRecoilState(objectfitState);
    const [isPlaying, setIsPlaying] = useRecoilState(isPlayingState);
    const [isVideoMenu, setIsVideoMenu] = useRecoilState(isVideoMenuState);

    useEffect(() => {
        const handlePlayerResize = () => {
            if (videoPlayerRef.current) {
                const devideWidth = window.innerWidth;
                const deviceHeight = window.innerHeight;
                setMaxWidth(videoPlayerRef.current.offsetWidth);
                setMaxHeight(videoPlayerRef.current.offsetHeight);
            }
        };
        window.addEventListener('resize', handlePlayerResize);
        handlePlayerResize();
        return () => {
            window.removeEventListener('resize', handlePlayerResize);
        };
    }, []);

    useEffect(() => {
        console.log('hls 렌더링1');
        if (Hls.isSupported()) {
            let hls;
            if (hlsRef.current == null) {
                hls = new Hls();
                hlsRef.current = hls;
            }
            hls.loadSource(videoUrl);
            hls.attachMedia(videoRef.current);
            hls.on(Hls.Events.MANIFEST_PARSED, (event, data) => {
                const availableLevels = data.levels.map((level, index) => ({
                    index,
                    width: level.width,
                    height: level.height,
                }));
                setResolutions(availableLevels);
                videoRef.current.play().then(() => {
                    console.log("attempting to play");
                }).catch(error => {
                    console.log("Error attempting to play");
                });
            });
        } else if (videoRef.current.canPlayType('application/vnd.apple.mpegurl')) {
            videoRef.current.src = videoUrl;
            videoRef.current.addEventListener('loadedmetadata', () => {
                setDuration(videoRef.current.duration);
                videoRef.current.play().then(() => {
                    console.log("attempting to play");
                }).catch(error => {
                    console.log("Error attempting to play");
                });
            });
        }
        return () => {
            if (hlsRef.current) {
                hlsRef.current.destroy();
                hlsRef.current = null;
            }
        };
    }, [videoUrl]);

    useEffect(() => {
        if (hlsRef.current) {
            hlsRef.current.currentLevel = selectedLevel;
        }
    }, [hlsRef, selectedLevel]);

    useEffect(() => {
        if (!isPlayerHovered && isVideoMenu) {
            setIsVideoMenu(false);
        }
    }, [isPlayerHovered]);

    useImperativeHandle(ref, () => ({
        handleVideoClick,

    }));

    function handleVideoClick() {
        if (videoRef.current.paused) {
            videoRef.current.play().then(() => {
                setIsPlaying(true);
            }).catch(error => {
                console.error('Error attempting to play:', error);
            });
        } else {
            setIsPlaying(false);
            videoRef.current.pause();
        }
    };

    return (
        <div className='VideoPlayer'
            ref={videoPlayerRef}
            onMouseEnter={() => setIsPlayerHovered(true)}
            onMouseLeave={() => setIsPlayerHovered(false)}
        >
            <video className='Video'
                ref={videoRef}
                onClick={handleVideoClick}
                style={{ objectFit: `${objectfit}`, width: `${maxWidth}px`, height: `${maxHeight}px` }}
                autoPlay
            />
            {
                isPlayerHovered &&
                <VideoMenu levels={resolutions} videoRef={videoRef} isVisible={isVideoMenu} />
            }
            <VideoController
                isPlayerHovered={isPlayerHovered}
                videoContainer={videoPlayerRef}
                video={videoRef}
                videoUrl={videoUrl}
                totaltime={duration}
            />
        </div>
    );
});

export default VideoPlayer;