import React, { useRef, useState, useEffect } from 'react';
import './PlayerPage.css';
import VideoPlayer from '../components/videoPlayer/VideoPlayer';
import VideoListView from '../components/videoList/VideoListView';
import { getVideoList, getStreamUrl } from '../utils/videoApi';

const PlayerPage = () => {
    const [video, setVideo] = useState('demo');
    const [videoList, setVideoList] = useState([]);
    useEffect(() => {
        getVideoList()
            .then(response => {
                setVideoList(response.data.list);
            })
            .catch(error => {
                console.error('Error fetching the video list:', error);
            });

    }, [videoList]);

    const handleVideoClick = (video) => {
        setVideo(video.video_id);
    };
    return (
        <div className='PlayerPage'>
            <div className='player'>
                <VideoPlayer videoUrl={getStreamUrl(video)} />
            </div>
            <div className='playlist'>
                <VideoListView videoList={videoList} handleVideoClick = {handleVideoClick}/>
            </div>
        </div>
    );
}

export default PlayerPage;