import { emptyImage } from '../../utils/videoApi';
import './VideoListView.css';


const VideoListView = ({ videoList, handleVideoClick }) => {
    return (
        <div className='VideoListView'>
            <hr style={{ width: '100%', color: '#666' }} />
            {
                videoList.length === 0
                    ? (
                        Array.from({ length: 8 }, (_, index) => (
                            <div className="ListItem" key={index}>
                                <div className='ThumbnailField'>
                                    <center><p>Empty Video</p></center>
                                    <img src={emptyImage} alt="Empty Thumbnail" className='thumbnail' />
                                </div>
                                <div className='profile'>
                                    <div className="text-fields">
                                        <p>Empty Title</p>
                                        <p>Unknown</p>
                                        <p>0 views • 0시간 전</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    )
                    : (
                        videoList.map(video => (
                            <div className="ListItem" key={video.video_id} onClick={() => handleVideoClick(video)}>
                                <div className='ThumbnailField'>
                                    <img src={video.thumbnail} alt="Thumbnail" className='thumbnail' />
                                </div>
                                <div className='profile'>
                                    <div className="text-fields">
                                        <p className='bold'>{video.video_id}</p>
                                        <p>{video.description}</p>
                                        <p>{video.user_id}</p>
                                        <p>{video.view_count} views • 12시간 전</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    )
            }
        </div>
    );
};


export default VideoListView;