import React, { useState, useEffect, useRef, useId } from 'react';
import { useNavigate } from 'react-router-dom';
import './LoginPage.css';
import SignUpPage from './Signup';
import PasswordReset from './PasswordReset';
import { postLogin } from '../../utils/userApi';


const LoginPage = () => {
    const navigate = useNavigate();
    const navigateToHome = () => {
        navigate('/');
    };
    const uid = useId();
    const loginRef = useRef(null);
    const [signup, setSignup] = useState(false);
    const [passwdReset, setPasswdReset] = useState(false);
    const [id, setId] = useState('');
    const [passwd, setPasswd] = useState('');
    const [errorMessage, setErrorMessage] = useState('');

    useEffect(() => {
        const element = loginRef.current;

        const handleTransitionEnd = () => {
            //setIsHidden(true);
        };

        element.addEventListener('transitionend', handleTransitionEnd);

        return () => {
            element.removeEventListener('transitionend', handleTransitionEnd);
        };
    }, []);


    const handleLogin = () => {
        // 로그인 기능을 구현하는 영역
        // 실제 API를 호출하여 access-token을 받아와서 처리하도록 한다.
        if (id === '' || passwd === '') {
            setErrorMessage('ID와 비밀번호를 입력해 주세요.')
        }
        else if (id === 'test' || passwd === 'test') {
            localStorage.setItem("access-token", "test.accesss.token")
            navigate('/');
        }
        else {
            try {
                postLogin(id, passwd).then((response) => {
                    console.log('LoginPage:response:', response.data);
                    localStorage.setItem("access-token", response.data.access_token);
                    localStorage.setItem("refresh-token", response.data.refresh_token);
                    navigate('/');
                }).catch(error => {
                    console.error('Error Login:', error);
                });

            } catch (error) {
                console.log(error);
            }
        }
    };

    const handleSignup = () => {
        setSignup(true);
    };

    const handleResetPassword = () => {
        setPasswdReset(true);
    };

    const handleSocialLogin = (agent) => {

    };

    return (
        <div className='login-page'>
            <SignUpPage signup={signup} setSignup={setSignup} />
            <div className='login-container'
                ref={loginRef}
                style={
                    (signup || passwdReset)
                        ? { transform: 'scale(0%)', opacity: '0', transition: 'transform 0.4s, opacity 0.4s  ease-in-out' }
                        : { transform: 'scale(100%)', opacity: '1', transition: 'transform 0.4s, opacity 0.4s  ease-in-out' }
                }
            >
                <h2><div className='Green' onClick={navigateToHome}>FactorzWeb</div> Login</h2>
                <br />
                <form id="login-form" onSubmit={(e) => e.preventDefault()}>
                    <label htmlFor={`${uid}-id`}>ID</label>
                    <input
                        type="text"
                        id={`${uid}-id`}
                        name="id"
                        value={id}
                        onChange={(e) => setId(e.target.value)}
                        required
                    />
                    <label htmlFor={`${uid}-passwd`}>Password</label>
                    <input
                        type="password"
                        id={`${uid}-passwd`}
                        name="passwd"
                        value={passwd}
                        onChange={(e) => setPasswd(e.target.value)}
                        required
                    />
                    <button type="button" onClick={handleLogin}>Login</button>
                </form>
                <div className="RowBox">
                    <div className='box' onClick={handleSignup}>회원가입</div>
                    <pre>/</pre>
                    <div className='box' onClick={handleResetPassword}>비밀번호 찾기</div>
                </div>
                {errorMessage && <div id="error-message" className='error'>{errorMessage}</div>}
                <h3>Social Login With</h3>
                <div className="RowBox">
                    <button className="naver-button" onClick={() => handleSocialLogin('naver')}>
                        <img src='../icons/naver.png'></img>
                        Naver
                    </button>
                    <button className="kakao-button" onClick={() => handleSocialLogin('kakao')}>
                        <img src='../icons/kakao_logo.png'></img>
                        Kakao
                    </button>
                    <button className="google-button" onClick={() => handleSocialLogin('google')}>
                        <img src='../icons/google.webp'></img>
                        Google
                    </button>
                </div>
            </div>
            <PasswordReset passwdReset={passwdReset} setPasswdReset={setPasswdReset} />
        </div>
    );
}
export default LoginPage;