const InputItem = ({ name, type, value, onChangeValue }) => {
    const id = useId();
    const isTel = type === 'tel';
    return (
        <div className='InputItem'>
            <label htmlFor={`${id}-${name}`}>{name} : </label>
            <input
                type={type}
                id={`${id}-${name}`}
                name={name}
                value={value}
                onChange={(e) => onChangeValue(e.target.value)}
                required={isTel}
                pattern={isTel ? "[0-9]{3}-[0-9]{3}-[0-9]{4}" : undefined}
                title={isTel ? "Phone number must be in the format: 123-456-7890" : undefined}
            />
        </div>
    );
};
export default SignupPage;