import axios from 'axios';

const BASE_API_URL = "http://dev.factorzzang.shop:10000";

export const postSignup = async (email, passwd, name, nick, mobile) => {
    try {
        const userData = {
            email: email,
            password: passwd,
            name: name,
            nick: nick,
            mobile: mobile
        };
        const response = await axios.post(`${BASE_API_URL}/user/account`, userData);
        return response; // 성공적으로 데이터를 받아왔을 때 반환
    } catch (error) {
        if (error.response) {
            console.error('Server responded with a status code:', error.response.status);
            console.error('Response data:', error.response.data);
        } else if (error.request) {
            console.error('No response received:', error.request);
        } else {
            console.error('Error setting up request:', error.message);
        }
        console.error('Error config:', error.config);
        throw error;
    }
};

