import React, { useState, useEffect, useRef, useId } from 'react';
import { useNavigate } from 'react-router-dom';
import './Signup.css';
import { postUserApi } from '../../utils/userApi';

const PasswordReset = ({ passwdReset, setPasswdReset }) => {
    const [email, setEmail] = useState('');
    const [passwd, setPasswd] = useState('');
    const [authCode, setAuthCode] = useState('');
    const [errorMsg, setErrorMsg] = useState('');

    const handleClickSet = () => {
        if (email === '' || passwd === '' || authCode === '') {

        }
        postUserApi().then((response) => {
            console.log(response.data);
            setAuthCode(response.data);
        }).catch(error => {
            console.log(error);
        });
    };

    const handleClickCancel = () => {
        setPasswdReset(false);
    }

    return (
        <div className='Signup'
            style=
            {
                passwdReset
                    ? { transform: 'translateX(-100%) scale(100%)', opacity: '1', transition: 'transform 0.8s, opacity 0.8s  ease-in-out' }
                    : { transform: 'translateX(0%) scale(100%)', opacity: '0', transition: 'transform 0.4s, opacity 0.4s  ease-in-out' }
            }
        >
            <center><h2><div className='Green' >Password</div> Reset</h2></center>
            <div className='InputBox' style={{ border: '1px solid #222', backgroundColor: '#1b9612' }}>
                <InputItem name="이메일" type="email" value={email} onChangeValue={setEmail} />
                <InputItem name="인증코드" type="password" value={passwd} onChangeValue={setPasswd} />
                <InputItem name="새암호" type="password" value={passwd} onChangeValue={setPasswd} />
            </div>
            <div className='BtnBox' style={{ marginTop: '10px' }}>
                <button onClick={handleClickSet}>코드 발급</button>
                <button onClick={handleClickCancel}>재설정</button>
                <button onClick={handleClickCancel}>취  소</button>
            </div>
        </div>
    );
};

const InputItem = ({ name, type, value, onChangeValue }) => {
    const id = useId();
    const isTel = type === 'tel';
    return (
        <div className='InputItem'>
            <label htmlFor={`${id}-${name}`}>{name} : </label>
            <input
                type={type}
                id={`${id}-${name}`}
                name={name}
                value={value}
                onChange={(e) => onChangeValue(e.target.value)}
                required={isTel}
                pattern={isTel ? "[0-9]{3}-[0-9]{3}-[0-9]{4}" : undefined}
                title={isTel ? "Phone number must be in the format: 123-456-7890" : undefined}
            />
        </div>
    );
};
export default PasswordReset;