import React, { useRef, useState, useEffect } from 'react';
import { useLocation, Link } from 'react-router-dom';
import './TestPage.css';
import '../components/WatchSection.css';
import VideoPlayer from '../components/videoPlayer/VideoPlayer';
import { getStreamUrl } from '../utils/videoApi';

const TestPage = () => {
    const videoRef = useRef(null);
    const [editUrl, setEditUrl] = useState(getStreamUrl('demo'));
    const [videoUrl, setVideoUrl] = useState(getStreamUrl('demo'));
    const handleChangeClick = () => {
        setVideoUrl(editUrl);
    };

    const handlePlayClick = () => {
        if (videoRef.current) {
            videoRef.current.handleVideoClick();
        }
    }

    useEffect(() => {

    }, []);

    return (
        <div className='test-container'>
            <div className='video-content'>
                <center><h1><Link to='/'><div className='Green'>Factorz</div></Link> Player</h1></center>
                <center>Video URL :
                    <input type="text"
                        id="editUrl"
                        name="editUrl"
                        value={editUrl}
                        onChange={(e) => setEditUrl(e.target.value)}
                        required
                    />
                    <button className='video-url' style={{ borderRadius: '0px' }} type="button" onClick={handleChangeClick}>Change</button>
                    <button className='video-url' type="button" onClick={handlePlayClick}>Play</button>
                </center>
                <br />
                <VideoPlayer videoUrl={videoUrl} ref={videoRef} />

                <div className='info'>
                    <h2>I remember you - Skidrow (Demo)</h2>
                    <div className='user-info'>
                        <div className='user-info'>
                            <img src='./icons/developer.png' />
                            <ul style={{ marginTop: '15px', paddingLeft: '10px', paddingRight: '20px' }}>
                                <li style={{ marginBottom: '5px' }}>Factorzzang</li>
                                <li>구독자 10만명</li>
                            </ul>
                            <button>구독</button>
                        </div>
                        <div className='user-info'>
                            <button>좋아요</button>
                            <button>공유</button>
                            <button>더보기</button>
                        </div>
                    </div>
                    <div className='more-info'>설명 및 더보기 버튼</div>
                </div>
            </div>
        </div >
    );
};

export default TestPage;