export async function getVideoList() {
    const VIDEO_API_URL = `${BASE_API_URL}/videos`;
    try {
        const response = await axios.get(VIDEO_API_URL);
        return response
    } catch (error) {
        if (error.response) {
            console.error('Error Response code :', error.response.status);
            console.error('Error Response data :', error.response.data);
        } else if (error.request) {
            console.error('Error Request:', error.request);
        } else {
            console.error('Error:', error.message);
        }
        throw error;
    }
}

export const getThumbnail = async (videoId, accessToken) => {
    try {
        const response = await axios.get(`${BASE_API_URL}/thumbnail`, {
            params: { cid: videoId },
            headers: { Authorization: `Bearer ${accessToken}` }
        });
        return response.data; // 성공적으로 데이터를 받아왔을 때 반환
    } catch (error) {
        if (error.response) {
            console.error('Server responded with a status code:', error.response.status);
            console.error('Response data:', error.response.data);
        } else if (error.request) {
            console.error('No response received:', error.request);
        } else {
            console.error('Error setting up request:', error.message);
        }
        console.error('Error config:', error.config);
        throw error;
    }
};

export const getStreamUrl = (videoId) => {
    return `${STREAM_URL}/${videoId}/hls.m3u8`;
};